$page-bg: #4682B4;
$game-bg: "http://www.scottishrugby.org/sites/default/files/hero-images/xqe7-xjv.jpeg";



/************* 
Base
**************/

html, body{
  counter-reset: hit;
  width:100%;
  height:100%;
  box-sizing:border-box;
  cursor: crosshair;
  background:$page-bg;
}

*, *:before, *:after{
  box-sizing:border-box;
  cursor: crosshair;
}

input{
    position:absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height:100%;
    appearance: none;
  &:focus {
    outline: none;
    appearance: none;
  }
}

a{
  color:red;
  text-decoration: none; 
}

