/************* 
modules
**************/

.game-header{
  padding:20px;
  font-size:30px;
  font-weight:bolder;
}

.game-header::after {
  content: counter(hit);
}

.game-row{
  width:100vw;
  height:200px;
  border-bottom: 5px solid brown;
  overflow: hidden;
  perspective: 1000px;
align-items: flex-end;
}

.duck-row{
  display:flex;
  flex-wrap:nowrap;
  margin-left: -4000px;
  width:5000px;
  height:200px;
  transform-style: preserve-3d;
