/************* 
States
**************/

input:checked {
  counter-increment: hit;
  &:after{
    content:"+1";
    position:relative;
    top: 10px; 
    left:0;
    right: 0;
    padding:10px;
    width:75px;
    height:75px;
    z-index:5;
    color:gold;
    font-size:30px;
    background:rgba(black, .6);
    border-radius:50%;
    animation: hit 2s forwards;
    
  }
  & ~.duck-row__img{
    background:url(http://cliparts.co/cliparts/zcX/oqo/zcXoqorgi.png);
    background-size:contain;
    &:after{
      content:"";
      position:absolute;
      top:20;
      left:500;
      width:inherit;
      height:inherit;
    }
  }
}



@keyframes move {
  0%{margin-left: -4000px;}
  100%{margin-left:2500px;}
}

@keyframes move2 {
  0%{margin-left: 2000px;}
  100%{margin-left:-4000px;}
}


@keyframes flow{
  0%{left:80px;}
  50%{left:65px}
}

@keyframes hit{
  0%{top: 10px;}
  100%{
    top:-100px;
    display: none;
    opacity: 0;
  }
}
